<template>
  <div class="odrderState" :style="item">
      <div class="odrderStateTop">
            <Goback title="订单详情"></Goback>
        <div class="odrderState_top">
            <div class="topView">
                <div class="top_left">
                    <div class="left_text">{{title}}</div>
                    <div class="left_state">{{titleState}}</div>
                </div>
                <div class="top_right" :style="stateNum==1?'width:92px;height:92px':''">
                    <img v-show="stateNum==1" src="@/assets/images/peopleCenter/detils1.png" alt="" />
                    <img v-show="stateNum==2" src="@/assets/images/peopleCenter/detils2.png" alt="" />
                    <img v-show="stateNum==3" src="@/assets/images/peopleCenter/detils3.png" alt="" />
                    <img v-show="stateNum==4" src="@/assets/images/peopleCenter/detils4.png" alt="" />
                </div>
            </div>
        </div>
        <div class="odrderState_box">
            <div class="box_main">
                <div class="main_item">
                    <p class="item_left">订单编号</p>
                    <p class="item_right">2020042900120012</p>
                </div>
                <div class="main_item">
                    <p class="item_left">案件类型</p>
                    <p class="item_right">行政案件</p>
                </div>
                <div class="main_item">
                    <p class="item_left">服务律所</p>
                    <p class="item_right">重庆启林律师事务所有限公司</p>
                </div>
                <div class="main_item">
                    <p class="item_left">商品金额</p>
                    <p class="item_right">2000元</p>
                </div>
                <div class="main_item">
                    <p class="item_left">下单用户</p>
                    <p class="item_right">ql_55jh5234</p>
                </div>
                <div class="main_mero" v-show="stateNum==1">更换支付方式 ></div>
            </div>
        </div>
      </div>
      <!-- 待付款 -->
      <!-- <div class="odrderStateBottom" v-if="stateNum==1">
          <div class="Bottom_left centerBoxMain">查看对公账户</div>
          <div class="Bottom_right centerBoxMain">我已转款</div>
      </div> -->
      <div class="odrderStateFl">
          <!-- 部分/全额付款 -->
          <!-- <template v-if="stateNum==2"> -->
              <!-- <div class="Fl_state2 centerBoxMain">催单</div> -->
          <!-- </template> -->
          <!-- 处理中 -->
          <!-- <template v-if="stateNum==3">
              <div class="Fl_left centerBoxMain">查看对公账户</div>
              <div class="Fl_right centerBoxMain">我已转款</div>
          </template> -->
          <!-- 已完结 -->
          <!-- <template v-if="stateNum==4"> -->
              <!-- <div class="Fl_left centerBoxMain" @click="()=>this.is_show=true">删除订单</div> -->
              <div class="Fl_right centerBoxMain">查看进度</div>
          <!-- </template> -->
      </div>
      <van-popup 
      v-model="is_show" 
      position="center" 
      round  
      :style="{ height: '30%' }" 
      >
      <div class="show">
          <img src="@/assets/images/totast.png" alt="" srcset="" />
          <div class="show_text">你确定要删除订单吗？</div>
          <div class="show_btn">
              <div class="btn1 centerBoxMain" @click="()=>this.is_show=false">取消</div>
              <div class="btn2 centerBoxMain">确定</div>
          </div>
      </div>
      </van-popup>
  </div>
</template>

<script>
export default {
    name:'odrderState',
    data(){
        return{
            title:'',
            titleState:'',
            stateNum:null,
            item:{height:'',width:''},
            is_show:false
        }
    },
   methods:{
        _setTitle(pageID){//设置标题
            if(pageID==1) return '待付款';
            if(pageID==2) return '部分/全额付款';
            if(pageID==3) return '处理中';
            if(pageID==4) return '已完结';
        },
        _setState(pageID){//设置页面状态
            if(pageID==1) {
                this.titleState= '剩余29分钟59秒关闭';
            }
            if(pageID==2) this.titleState='等待服务中~'
            if(pageID==3) this.titleState='服务正在处理中~'
            if(pageID==4) this.titleState='本次服务已圆满完成'
        }
   },
    created(){
        const { viewID } = this.$route.query
        this.stateNum=viewID
        this.title=this._setTitle(viewID)
        this._setState(viewID)
        this.item = {
            height: document.documentElement.clientHeight + "px",
            width: document.documentElement.clientWidth + "px"
        };
    }
}
</script>

<style lang="scss">
@import './index.scss';

</style>


